.sidebar{
    display: block;
    
    .vertical{
        display: flex;
        position: absolute;
        top: 55vh;
        left: 3vw;
        z-index: 10;
        width: 500.297px;
        height:50px;
        transform: translate(0%,-50%);
        
        ul{
            display: flex;
            flex-direction: row;
            transform: rotate(-90deg);
            list-style-type: none; 
            font-size: 1.8rem;
            font-weight: 600;
            padding: 20px 0;
            margin: 0 0;
            margin-left: -25%;
            a{     
                text-decoration: none; 
                padding: 0 0;
                margin: 0 0;    
                &:first-child li{
                    padding-left: 0rem;
                    &:after{
                        content:'';
                        position: absolute;
                        width:0;
                        height:2px;
                        display:block;
                        margin-top: 5px;
                        left: 0;
                        background:$white;
                        transition: 0.4s ease;
                        color: $white;
                    }
                    &:hover{
                        &:after{
                            width: 27%;
                            left:0;
                            background:$white;
                        }
                    }
                  } 
                  &:last-child{
                    &:after{
                        content:'';
                        position: absolute;
                        width:0;
                        height:2px;
                        display:block;
                        margin-top: 5px;
                        right: 0;
                        background:$white;
                        transition: 0.4s ease;
                        color: $white;
                    }
                    &:hover{
                        &:after{
                            width: 50%;
                            right:0;
                            background:$white;
                        }
                    }
                  }
                li{
                    padding-left: 5rem;
                    color: $white;
                 
                }
            }
        }
    }
    @include media('<=phone') {
        display: none;
    }
}